import React from 'react'


const CalendarOption = props => {

    const chooseAllDate = () => {
        props.func([0, 0])
        props.setListVisible(false)
    }

    const chooseToday = () => {
        //     
        const first = new Date()
        //     
        first.setHours(0, 0, 0, 0)
        //     
        const second = new Date()
        //     
        second.setHours(23, 59, 59, 999)
        //   
        props.func([first, second])
        //   
        props.setListVisible(false)
    }

    const chooseThisWeek = () => {
        //     
        const first = new Date()
        //     
        first.setTime(first.getTime() - (first.getDay() - 1 || 7) * 24 * 60 * 60 * 1000)
        //     
        first.setHours(0, 0, 0, 0)
        //     
        const second = new Date()
        //    
        // second.setDate(first.getDate() + 6)
        //     
        // second.setHours(23,59,59,999)
        //   
        props.func([first, second])
        //   
        props.setListVisible(false)
    }

    const chooseThisMonth = () => {
        //     
        const first = new Date()
        //     
        first.setDate(1)
        //     
        first.setHours(0, 0, 0, 0)
        //     
        const second = new Date()
        //    
        // second.setMonth(first.getMonth() + 1)
        // second.setDate(1)
        // second.setTime(second.getTime() - 24 * 60 * 60 * 1000)
        //     
        // second.setHours(23,59,59,999)
        //   
        props.func([first, second])
        //   
        props.setListVisible(false)
    }

    const chooseThisYear = () => {
        //     
        const first = new Date()
        //     
        first.setMonth(0)
        first.setDate(1)
        first.setHours(0, 0, 0, 0)
        //     
        const second = new Date()
        //   
        props.func([first, second])
        //   
        props.setListVisible(false)

    }

    const chooseLastWeek = () => {
        //     
        const first = new Date()
        //     
        first.setTime(first.getTime() - ((first.getDay() - 1 || 7) + 7) * 24 * 60 * 60 * 1000)
        //     
        first.setHours(0, 0, 0, 0)
        //     
        const second = new Date()
        //     
        second.setDate(first.getDate() + 6)
        //     
        second.setHours(23,59,59,999)
        //   
        props.func([first, second])
        //   
        props.setListVisible(false)
    }

    const chooseLastMonth = () => {
        //     
        const first = new Date()
        //      
        first.setMonth(first.getMonth() - 1)
        first.setDate(1)
        first.setHours(0, 0, 0, 0)
        //     
        const second = new Date()
        //    
        second.setMonth(first.getMonth() + 1)
        second.setDate(1)
        second.setTime(second.getTime() - 24 * 60 * 60 * 1000)
        second.setHours(23,59,59,999)
        //   
        props.func([first, second])
        //   
        props.setListVisible(false)
    }

    const chooseDate = () => {
        props.setListVisible(false)
        props.setVisibleCalendar(true)
    }


    return props.invisible ? null : (
        <div
            id='listCalendarOption'
            className='listOptionsChoose'
            style={{width: props.width}}
        >
            {props.allDate ? <div className='options' onClick={chooseAllDate}> </div> : null}
            <div className='options' onClick={chooseToday}></div>
            <div className='options' onClick={chooseThisWeek}> </div>
            <div className='options' onClick={chooseThisMonth}> </div>
            <div className='options' onClick={chooseThisYear}> </div>
            <div className='options' onClick={chooseLastWeek}> </div>
            <div className='options' onClick={chooseLastMonth}> </div>
            <div className='options' onClick={chooseDate}> </div>
        </div>
    )
}


export default CalendarOption